<template lang="html">
  <div class="footer">
    <mt-tabbar v-model="selected" fixed class="border-1px-top">
      <mt-tab-item id="Category" >
        <img :src="img1" slot="icon">目录
      </mt-tab-item>
      <mt-tab-item id="Next" >
        <img :src="img3" slot="icon">下一章
      </mt-tab-item>
      <mt-tab-item id="Prev" >
        <img :src="img4" slot="icon">上一章
      </mt-tab-item>
    </mt-tabbar>
  </div>
</template>

<script>
export default {
  data () {
    return {
      selected: "首页",
      img1: "static/icon/indexchoose.png",
      img2: "static/icon/brand.png",
      img3: "static/icon/live.png",
      img4: "static/icon/cart.png",
      img5: "static/icon/me.png"
    }
  },
  computed: {
    otherSelect () {
      return this.$store.state.selectTab
    }
  },
  watch: {
    // 监听 是否从其他的页面跳转过来的数据
    "otherSelect" () {
      this.selected = this.otherSelect
    },
    // this.$.router.push({name: ''})
    // 固定的书写 ""双引号里面是要监听的数据 handler是处理数据改变的函数 deep是否深度监听
    "selected": {
      handler (val,oldval) { //多看文档 handler
        if (this.selected == "Category") {
          this.$router.push('/')
          this.img1="static/icon/indexchoose.png"
        } else {
          this.img1="static/icon/index.png"
        }
        if (this.selected == "Next" && this.$route.path.search(/content/)!==-1) {
          var i = parseInt(this.$route.path.substring(this.$route.path.lastIndexOf('content')+7))
          i = i + 1
          if(i<286){
            this.$router.push('/index/content'+i)
            this.img3="static/icon/livechoose.png"
          }
          this.selected = ""
        } else {
          this.img3="static/icon/live.png"
        }
        if (this.selected == "Prev" && this.$route.path.search(/content/)!==-1) {
          var i = parseInt(this.$route.path.substring(this.$route.path.lastIndexOf('content')+7))
          i = i - 1
          if(i>0){
            this.$router.push('/index/content'+i)
            this.img4="static/icon/cartchoose.png"
          }
          this.selected = ""
        } else {
          this.img4="static/icon/cart.png"
        }
      }
    }
  },
}
</script>

<style lang="stylus" type="stylesheet/stylus" scoped>
@import '../common/stylus/mixin.styl'
.mint-tabbar
  background #fff
  border-1px-top()
  height 1.5625rem
  padding-top -.4rem
  .mint-tab-item.is-selected
    color #FE4070
    background #fff
    text-decoration none
</style>



